//
// Page with a list of trips for the user
//
import React, { Fragment, useEffect } from 'react';
import { connect } from 'react-redux';
import { fetchTrips, fetchCities, createTrip, fetchFriends, updateTrip, deleteTrip } from '../actions';

import differenceInDays from 'date-fns/difference_in_days';

import NewTrip from '../components/NewTrip.react';
import TripCard from '../components/TripCard.react';
import Spinner from '../components/Spinner.react';

function TripDashboard(props) {
  // Get data out of redux
  const { cities, trips, users } = props;

  // Load API data for page
  useEffect(
    () => {
      if (!cities.isFetching && !cities.hasFetched) {
        props.fetchCities();
      }
      if (!trips.isFetching && !trips.hasFetched) {
        props.fetchTrips();
      }
    },
    []
  );

  const populatedTrips = trips.ids.map(tId => {
    const trip = trips.all[tId];
    return {
      ...trip,
      city: cities.all[trip.city],
      matches: trip.matches.map(match => {
        const [uId, start, end] = match.split('::');
        return {
          ...users.all[uId],
          start_date: start,
          end_date: end
        };
      })
    };
  });

  // Sort so the soonest trip is first in the list
  populatedTrips.sort((a, b) => differenceInDays(a.start_date, b.start_date));

  return (
    <Fragment>

      <h4>ADD NEW TRIP</h4>
      <NewTrip
        cities={cities}
        isLoadingCities={cities.isFetching}
        onCreateTrip={props.createTrip}
      />

      <TripsList
        trips={populatedTrips}
        updateTrip={props.updateTrip}
        deleteTrip={props.deleteTrip}
      />

    </Fragment>
  );
}

function TripsList({ trips, updateTrip, deleteTrip }) {
  if (trips.isFetching) {
    return <Spinner />;
  }

  if (trips.length === 0) {
    return <p>
      You have no upcoming trips. Hope you enjoy your rest.
    </p>;
  }

  // TODO: Pagination / see historic etc

  return <Fragment>
    <h4>UPCOMING TRIPS</h4>
    {trips.map(trip => 
      <TripCard
        key={trip.id}
        onUpdate={updateTrip}
        onDelete={deleteTrip}
        {...trip}
      />
    )}
  </Fragment>;
}

const mapStateToProps = ({ cities, trips, users }) => ({
  cities,
  trips,
  users,
});

export default connect(
  mapStateToProps,
  {
    fetchTrips,
    fetchCities,
    fetchFriends,
    createTrip,
    updateTrip,
    deleteTrip
  }
)(TripDashboard);
