import React from 'react';
import { Link } from 'react-router-dom';
import { withCookies } from 'react-cookie';

// TODO: Make mobile-specific version.
const Header = ({ cookies, onLogout, activeUser }) => (
  <div className='uk-navbar-container'>
    <nav className='uk-navbar uk-container'>
      <div className='uk-navbar-left'>
        <Link className='uk-navbar-item uk-logo' to='/'>
          <img src='/logo.png' alt='logo' style={{ height: 35, paddingRight: 15 }} />
          Locationless
        </Link>
      </div>
      
      {(cookies.get('sessionid') && activeUser) ?
        <div className='uk-navbar-right'>
          <ul className='uk-navbar-nav'>
            <li className='uk-navbar-item'>
              <Link to='/'>Dashboard</Link>
            </li>
            <li className='uk-navbar-item'>
              <Link to='/friends'>Friends</Link>
            </li>
            <li className='uk-navbar-item'>
              <Link to={`/users/${activeUser}`}>Profile</Link>
            </li>
            <li className='uk-navbar-item'>
              <a onClick={onLogout}>Logout</a>
            </li>
          </ul>
        </div> :
        null
      }
    </nav>
  </div>
);

export default withCookies(Header);
