import {
  API,

  FETCH_TRIPS,
  FETCH_TRIP,
  SET_TRIPS,
  CREATE_TRIP,
  UPDATE_TRIP,
  DELETE_TRIP,
  REMOVE_TRIP,

  FETCH_FRIENDS,
  CREATE_FRIEND_REQ,
  ACCEPT_FRIEND_REQ,

  FETCH_USER,
  SET_USERS,

  FETCH_CITIES,
  SET_CITIES,
} from './types';

import { apiUrl } from '../config';

export function fetchFriends() {
  return apiAction({
    url: `${apiUrl}/u/friends/`,
    onSuccess: setUsers,
    onFailure: () => console.log('Error occured loading friends'),
    label: FETCH_FRIENDS,
  });
}

export function acceptFriendRequest(data) {
  return apiAction({
    url: `${apiUrl}/u/${data.id}/friends/`,
    method: 'PUT',
    // HACK: Need the API to return the updated user object
    onSuccess: () => setUsers({
      data: {
        ...data,
        is_friend: true,
        is_pending_friend: false
      }
    }),
    nextAction: fetchTrips,
    onFailure: () => console.log('Error occured accepting friendship request'),
    data: data,
    label: ACCEPT_FRIEND_REQ,
  });
}

export function requestFriendship(data) {
  return apiAction({
    url: `${apiUrl}/u/${data.id}/friends/`,
    method: 'POST',
    // HACK: Need the API to return the updated user object
    onSuccess: () => setUsers({
      data: {
        ...data,
        have_requested_friend: true
      }
    }),
    onFailure: () => console.log('Error occured requesting friendship'),
    data: data,
    label: CREATE_FRIEND_REQ,
  });
}

export function fetchUser(id) {
  return apiAction({
    url: `${apiUrl}/u/${id}/`,
    onSuccess: setUsers,
    onFailure: () => console.log('Error occured loading user', id),
    label: FETCH_USER,
  });
}

export function setUsers(data) {
  return {
    type: SET_USERS,
    payload: data,
  };
}

export function fetchCities() {
  return apiAction({
    url: `${apiUrl}/cities/`,
    onSuccess: setCities,
    onFailure: () => console.log('Error occured loading cities'),
    label: FETCH_CITIES,
  });
}

function setCities(data) {
  return {
    type: SET_CITIES,
    payload: data,
  };
}

export function fetchTrip(id) {
  return apiAction({
    url: `${apiUrl}/trips/${id}`,
    onSuccess: setTrips,
    onFailure: () => console.log('Error occured loading trips'),
    label: FETCH_TRIP,
  });
}

export function fetchTrips() {
  return apiAction({
    url: `${apiUrl}/trips/`,
    onSuccess: setTrips,
    onFailure: () => console.log('Error occured loading trips'),
    label: FETCH_TRIPS,
  });
}

function setTrips(data) {
  return {
    type: SET_TRIPS,
    payload: data,
  };
}

function removeTrip(data) {
  return {
    type: REMOVE_TRIP,
    payload: data,
  };
}

export function createTrip(data) {
  return apiAction({
    url: `${apiUrl}/trips/`,
    method: 'POST',
    onSuccess: setTrips,
    onFailure: () => console.log('Error occured creating trip'),
    data: data,
    label: CREATE_TRIP,
  });
}

export function updateTrip(data) {
  return apiAction({
    url: `${apiUrl}/trips/${data.id}`,
    method: 'PUT',
    onSuccess: setTrips,
    onFailure: () => console.log('Error occured updating trip'),
    data: data,
    label: UPDATE_TRIP,
  });
}

export function deleteTrip(data) {
  return apiAction({
    url: `${apiUrl}/trips/${data.id}`,
    method: 'DELETE',
    onFailure: () => console.log('Error occured deleting trip'),
    onSuccess: () => removeTrip(data),
    label: DELETE_TRIP,
  });
}

function apiAction({
  url = '',
  method = 'GET',
  data = null,
  accessToken = null,
  onSuccess = () => {},
  onFailure = () => {},
  nextAction = () => {},
  label = '',
  headersOverride = null
}) {
  return {
    type: API,
    payload: {
      url,
      method,
      data,
      accessToken,
      onSuccess,
      onFailure,
      label,
      headersOverride,
      nextAction
    }
  };
}
