import React, { useEffect } from 'react';
import { connect } from 'react-redux';

import {
  fetchUser,
  requestFriendship,
  acceptFriendRequest
} from '../actions';

import Spinner from '../components/Spinner.react';
import FriendStatusButton from '../components/FriendStatusButton.react';
import LocationMap from '../components/LocationMap.react';

const UserProfile = ({ match, users, fetchUser, activeUser, requestFriendship, acceptFriendRequest }) => {
  const userId = parseInt(match.params.id, 10);

  // Clone the trip
  let user = users.all[userId];

  useEffect(
    () => {
      // Get it from the API if:
      //   - it is not currently fetching users
      //   - we don't have the user in the store already
      if (!users.isFetching && !user) {
        fetchUser(userId);
      }
    },
    []
  );

  return <MainPanel
    isFetching={users.isFetching}
    user={user}
    activeUser={activeUser}
    onRequest={() => requestFriendship(user)}
    onAccept={() => acceptFriendRequest(user)}
  />;
};

const MainPanel = ({ isFetching, user, activeUser, onRequest, onAccept }) => {
  if (isFetching || !user) {
    return <Spinner />;
  }

  return <div className='centered-hero'>
    <div className='uk-flex uk-flex-left'>
      <img
        className='avatar-big uk-border-circle'
        alt='avatar'
        src={user.photo_url || '/blank-profile-picture.png'}
      />

      <div className='uk-flex uk-flex-column uk-padding'>
        <h4>@{user.username}</h4>
        <span>{user.first_name.toUpperCase()} {user.last_name.toUpperCase()}</span>
      </div>

      <div className='user-icons'>
        <FriendStatusButton user={user} onRequest={onRequest} onAccept={onAccept} />
      </div>
    </div>

    <hr />

    <LocationMap userId={user.id} />
  </div>;
}

const mapStateToProps = ({ users, activeUser }) => ({
  users,
  activeUser
})

export default connect(
  mapStateToProps,
  {
    fetchUser,
    requestFriendship,
    acceptFriendRequest
  }
)(UserProfile);
