const SERVER_ERR_MESSAGE = 'Looks like we\'re a little... LOCATIONLESS ourselves. Sorry about that.';
const NOT_FOUND_ERR_MESSAGE = 'Nothing to see here.';

export function reduceError(state, action) {
  const err = action.error && action.error.response;
  if (!err || (err.status >= 500) || !err.status) {
    return {
      ...state,
      error: {
        status: 500,
        message: SERVER_ERR_MESSAGE,
      }
    };
  }

  if (err.status === 404) {
    return {
      ...state,
      error: {
        status: 404,
        message: NOT_FOUND_ERR_MESSAGE,
      }
    };
  }

  return {
    ...state,
    error: {
      status: err.status,
      message: (err.data && err.data.error) || SERVER_ERR_MESSAGE,
    }
  };
}

