import React, { useState } from 'react';
import { List } from 'react-virtualized';
import ReactCountryFlag from 'react-country-flag';
import format from 'date-fns/format';
import addDays from 'date-fns/add_days'

import Select from 'react-select';
import Flatpickr from 'react-flatpickr'

import 'flatpickr/dist/themes/light.css'

const convertToSelectOpts = i => i.map(i => ({
  label: <div><ReactCountryFlag code={i.country_code} svg /> {i.name}</div>,
  value: `${i.name}:::${i.id}`
}));

//
// Virtualized Select Component
//
const MenuList = props => {
  const rows = props.children;
  const rowRenderer = ({ key, index, isScrolling, isVisible, style }) => (
    <div key={key} style={style}>{rows[index]}</div>
  );

  return (
    <List
      style={{ width: '100%', flexGrow: true }}
      width={props.width}
      height={300}
      rowHeight={35}
      rowCount={rows.length || 0}
      rowRenderer={rowRenderer}
    />
  );
}

function NewTrip ({ cities, onCreateTrip }) {
  const [city, setCity] = useState(null);
  const [dates, setDates] = useState([Date.now(), addDays(Date.now(), 7)]);

  //
  // Package up the state as a trip object and pass it back up
  //
  const createTrip = (e) => {
    e.preventDefault();
    onCreateTrip({
      city: parseInt(city.value.split(':::')[1], 10),
      start_date: format(dates[0], 'YYYY-MM-DD'),
      end_date: format(dates[1], 'YYYY-MM-DD'),
    });
    setCity(null);
  };

  const citiesList = Object.keys(cities.all)
    .map(cId => cities.all[cId]);

  // Order cities by population for now
  citiesList.sort((a, b) => b.population - a.population);

  return <form className='uk-tile uk-padding-small uk-flex uk-flex-around uk-flex-middle'>
    <Select
      styles={{
        container: () => ({
          width: 250
        })
      }}
      components={{
        MenuList: (props)=> <MenuList {...props} width={window.innerWidth} />
      }}
      placeholder='New Trip Location'
      className='-select'
      isSearchAble
      isLoading={cities.isFetching}
      value={city}
      onChange={setCity}
      options={convertToSelectOpts(citiesList)}
    />
      <Flatpickr
        className='uk-input uk-width-1-3'
        options={{
          mode: 'range'
        }}
        value={dates}
        onChange={setDates}
      />
    <button
      className='uk-button uk-button-secondary'
      onClick={createTrip}
      disabled={city === null}
    >
      <span className='icono-plus'/>
    </button>
  </form>;
}

export default NewTrip;
