import React, { Component, Fragment } from 'react';
import { connect } from 'react-redux';
import { withCookies } from 'react-cookie';
import { withRouter, Route } from 'react-router-dom';
import axios from 'axios';

import TripDashboard from './pages/TripDashboard.react';
import TripDetail from './pages/TripDetail.react';
import FriendsList from './pages/FriendsList.react';
import UserProfile from './pages/UserProfile.react';

import PrivateRoute from './components/PrivateRoute.react';
import Login from './components/Login.react';
import Header from './components/Header.react';
import Spinner from './components/Spinner.react';

import { setUsers } from './actions';

import { apiUrl } from './config';

import './App.css';
import 'uikit/dist/css/uikit.css'

class App extends Component {
  onLogout() {
    this.props.cookies.remove('sessionid');
    axios.post(`${apiUrl}/u/logout/`);
    this.props.history.push('/login');
  }

  onLogin(data) {
    this.props.history.push('/');
    this.props.setUsers({
      data: {
        ...data,
        is_you: true
      }
    });
  }

  componentDidMount() {
    const { activeUser, location } = this.props;

    if (!activeUser && location.pathname !== '/login') {
      axios.get('/u/')
        .then(({ data }) => this.props.setUsers({ data: data.data }))
        .catch(err => {
          this.props.history.push('/login');
        });
    }
  }

  renderPrivateRoutes() {
    return <Fragment>
      <PrivateRoute exact path="/" component={TripDashboard} />
      <PrivateRoute path="/trips/:id" component={TripDetail} />
      <PrivateRoute exact path="/friends" component={FriendsList} />
      <PrivateRoute path="/users/:id" component={UserProfile} />
    </Fragment>;
  }

  renderError(msg) {
    return <main className='uk-container uk-container-small uk-container-center uk-padding'>
      <a href='/'>
        <img src='/logo.png' alt='logo' style={{ height: 85, padding: 15 }} />
      </a>
      <hr />
      <p>{msg}</p>
    </main>;
  }

  render() {
    if (this.props.error.message) {
      return this.renderError(this.props.error.message);
    }

    if (!this.props.activeUser && this.props.location.pathname !== '/login') {
      return <Spinner />;
    }

    return (
        <Fragment>
          <Header onLogout={this.onLogout.bind(this)} activeUser={this.props.activeUser} />
          <main className='uk-container uk-container-small uk-container-center uk-padding'>
            <Route path="/login" component={() => <Login onLogin={this.onLogin.bind(this)} />} />

            {this.renderPrivateRoutes()}
          </main>
        </Fragment>
    );
  }
}

const mapStateToProps = ({ users, activeUser, error }) => ({
  users,
  activeUser,
  error
});

export default withRouter(withCookies(
  connect(mapStateToProps, {
    setUsers
  })(App)
));
