import React, { Fragment, useState } from 'react';
import { withRouter, Link } from 'react-router-dom';

import ReactCountryFlag from 'react-country-flag';
import Flatpickr from 'react-flatpickr';

import getMonth from 'date-fns/get_month'
import getDate from 'date-fns/get_date'
import differenceInDays from 'date-fns/difference_in_days'
import format from 'date-fns/format';

import FriendChip from './FriendChip.react';

const MONTHS = [
  'JAN',
  'FEB',
  'MAR',
  'APR',
  'MAY',
  'JUN',
  'JUL',
  'AUG',
  'SEP',
  'OCT',
  'NOV',
  'DEC'
];

const DateRange = ({ start_date, end_date }) =>
    <div className='uk-flex date-range'>
      <div>
        <b>{getDate(start_date)} <br/> {MONTHS[getMonth(start_date)]}</b>
      </div>
      <div>
        <b>{getDate(end_date)} <br/> {MONTHS[getMonth(end_date)]}</b>
      </div>
      <p
        style={{
          width: 92,
          height: 42,
          fontSize: 16,
          textAlign: 'center',
          margin: 2,
          marginTop: 10
       }}
      >
        {differenceInDays(end_date, start_date)} Day Trip
      </p>
  </div>;

function TripCard({ id, city, start_date, end_date, matches, history, onUpdate, onDelete }) {

  const [editMode, setEditMode] = useState(false);
  const [promptDel, setPromptDel] = useState(false);
  const [dates, setDates] = useState([new Date(start_date), new Date(end_date)]);

  const updateTrip = () => {
    setEditMode(false);
    return onUpdate({
      id,
      city: city.id,
      start_date: format(dates[0], 'YYYY-MM-DD'),
      end_date: format(dates[1], 'YYYY-MM-DD'),
    });
  };

  const deleteTrip = () => onDelete({ id });

  if (promptDel) {
    return <Fragment>
      <div className='uk-tile uk-padding-small uk-flex uk-flex-between uk-background-muted'>
        <div>
          <h6>
             <ReactCountryFlag code={city.country_code} svg /> | {city.name}
          </h6>
        </div>
          <p>Confirm you would like to delete this trip</p>
          <span className='uk-flex'>
            <hr className='uk-divider-vertical uk-margin-right uk-margin-left' />
            <span className='uk-flex uk-flex-column'>
              <span className='icon-sm'>
                <span className='icono-check' onClick={deleteTrip} />
              </span>
              <span className='icon-sm'>
                <span className='icono-cross' onClick={(e) => setPromptDel(false)} />
              </span>
            </span>
          </span>
      </div>
      <hr />
    </Fragment>;
  }

  if (editMode) {
    return <Fragment>
      <div className='uk-tile uk-padding-small uk-flex uk-flex-between uk-background-muted'>
        <div>
          <h6>
             <ReactCountryFlag code={city.country_code} svg /> | {city.name}
          </h6>
        </div>
          <Flatpickr
            className='uk-input uk-width-1-3'
            options={{ mode: 'range' }}
            value={dates}
            onChange={setDates}
          />
          <span className='uk-flex'>
            <hr className='uk-divider-vertical uk-margin-right uk-margin-left' />
            <span className='uk-flex uk-flex-column'>
              <span className='icon-sm'>
                <span className='icono-check' onClick={updateTrip} />
              </span>
              <span className='icon-sm'>
                <span className='icono-cross' onClick={(e) => setEditMode(!editMode)} />
              </span>
            </span>
          </span>
      </div>
      <hr />
    </Fragment>;
  }

  return <Fragment>
    <div
      className='uk-tile uk-padding-small uk-flex uk-flex-between uk-link-muted'
    >
      <Link to={`/trips/${id}`} >
        <h6>
           <ReactCountryFlag code={city.country_code} svg /> | {city.name}
        </h6>
        <DateRange start_date={start_date} end_date={end_date} />
      </Link>
      <span className='uk-flex'>
        <span className='uk-flex'>
          {matches.slice(0, 3).map(m => <FriendChip key={m.id} {...m} />)}
          <ExtraFriends allFriends={matches} numDisplayed={3} />
        </span>
        <span className='uk-flex'>
            <hr className='uk-divider-vertical uk-margin-right uk-margin-left' />
          <span className='uk-flex uk-flex-column' >
            <span className='icon-sm'>
              <span className='icono-gear' onClick={(e) => {e.preventDefault();setEditMode(!editMode);}} />
            </span>
            <span className='icon-sm'>
              <span className='icono-trash' onClick={(e) => {e.preventDefault();setPromptDel(true);}} />
            </span>
          </span>
        </span>
      </span>
    </div>
    <hr />
  </Fragment>;
}

const ExtraFriends = ({ allFriends, numDisplayed }) => {
  if (allFriends.length > numDisplayed) {
    return <div className='uk-border-circle avatar-preview uk-background-muted'>
      <p className='uk-border-circle uk-dark'>
        +{allFriends.length - numDisplayed}
      </p>
    </div>;
  }
  return null;
};

export default withRouter(TripCard);
