import React, { useEffect, useState } from 'react';
import axios from 'axios';
import { Map, Marker, Popup, TileLayer, Polyline } from 'react-leaflet'
import { divIcon } from 'leaflet';

import Spinner from '../components/Spinner.react';

// Custom Icon for the leaftlet map.
const locationIcon = divIcon({
  html: '<span class="icono-caretDown"/>',
  iconSize: [25, 25],
  iconAnchor: [17, 24],
  popupAnchor: [-3, -26],
});
 
const LocationMap = ({ userId }) => {
  const [locations, setLocations] = useState(null);

  const fetchUserLocations = (id) => {
    if (!id) {
      return;
    }
    setLocations(null);
    return axios(`/u/${id}/locations/`)
      .then(res => {
        if (res.status === 200) {
          setLocations(res.data.data);
        } else if (res.status === 404) {
          setLocations([]);
        }
      });
  };

  useEffect(
    () => {
      fetchUserLocations(userId);
    },
    [userId]
  );

  // TODO: timing calcs
  const pLocations = []; // locations.filter();
  const fLocations = locations; // .filter();
  // const cLocation = null; // locations.find();

  const position = [51.505, -0.09];

  if (locations === null) {
    return <Spinner />;
  }

  return <Map center={position} zoom={1} style={{ height: 300 }}>
    <TileLayer
      url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
      attribution="&copy; <a href=&quot;http://osm.org/copyright&quot;>OpenStreetMap</a> contributors"
    />
    <Polyline
      positions={pLocations.map(({ city }) => [city.latitude, city.longitude])}
    />
    <Polyline
      positions={fLocations.map(({ city }) => [city.latitude, city.longitude])}
      color='#bf4e4e'
    />
    {locations.map(({ id, city, start_date, end_date }) => (
      <Marker
        key={id}
        position={[city.latitude, city.longitude]}
        icon={locationIcon}
      >
        <Popup>{city.name}</Popup>
      </Marker>
    ))}
  </Map>
};

export default LocationMap;

