import React, { Fragment, useEffect } from 'react';
import { connect } from 'react-redux';
import { fetchTrip } from '../actions';

import TripPanel from '../components/TripPanel.react';
import Spinner from '../components/Spinner.react';

const TripDetail = ({ match, trips, cities, users, fetchTrip }) => {
  const tripId = parseInt(match.params.id, 10);

  // Clone the trip
  let trip = trips.all[tripId];
  if (trip) {
    trip = {
      ...trip,
      city: cities[trip.city],
      matches: trip.matches.map(match => {
        const [uId, start, end] = match.split('::');
        return {
          ...users.all[uId],
          start_date: start,
          end_date: end
        };
      })
    };
  }

  useEffect(
    () => {
      // Get it from the API if:
      //   - it is not currently fetching trips
      //   - we don't have the trip in the store already
      if (!trips.isFetching && !trip) {
        fetchTrip(tripId);
      }
    },
    []
  );

  return <Fragment>
    <h4>TRIP SUMMARY</h4>
    <MainPanel isFetching={trips.isFetching} trip={trip} />
  </Fragment>
};

const MainPanel = ({ isFetching, trip }) => {
  if (isFetching || !trip) {
    return <Spinner />;
  }
  return <TripPanel {...trip} />;
}

const mapStateToProps = ({ trips, users, cities }) => ({
  trips,
  users,
  cities: cities.all
})

export default connect(
  mapStateToProps,
  {
    fetchTrip
  }
)(TripDetail);
